"use client";

import { useState } from "react";

export default function Newsletter() {
  const [email, setEmail] = useState("");
  const [sent, setSent] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // store locally as a lightweight placeholder
    try {
      const list = JSON.parse(localStorage.getItem("newsletter-list" ) || "[]");
      list.push({ email, date: new Date().toISOString() });
      localStorage.setItem("newsletter-list", JSON.stringify(list));
    } catch (e) {}
    setSent(true);
    setEmail("");
  };

  return (
    <div className="max-w-2xl mx-auto">
      <h3 className="text-2xl font-semibold text-slate-900">Stay in the loop</h3>
      <p className="mt-2 text-slate-600">Join our newsletter for occasional updates and product news.</p>

      {sent ? (
        <div className="mt-4 rounded-md bg-green-50 p-4 text-green-800">Thanks â€” you'll hear from us soon.</div>
      ) : (
        <form onSubmit={handleSubmit} className="mt-4 flex gap-2">
          <label htmlFor="nl-email" className="sr-only">Email</label>
          <input
            id="nl-email"
            type="email"
            required
            placeholder="you@example.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="flex-1 rounded-md border border-slate-200 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-200"
          />

          <button className="rounded-md bg-indigo-600 text-white px-4 py-2 hover:bg-indigo-700" type="submit">Subscribe</button>
        </form>
      )}
    </div>
  );
}
